::-moz-selection {
    background: transparent;
}

::selection {
    background: transparent;
}

.bg {
    background-position:center center;
    background-repeat:no-repeat;
    background-size:cover; /* you change this to "contain" if you don't want the images to be cropped */
    color:#fff;
}

.container {
    position: relative;
    width: 100%;
}


